import { useEffect, useState } from "react"
import React from "react";
import {VscCircle} from 'react-icons/vsc'
import { RxCross2} from 'react-icons/rx'
const Main = () => {
    const[arrData, setArrData] = useState([0, 0, 0, 0, 0, 0, 0, 0, 0]) // 틱택토 승리 판정 배열
    const[oxIcon, setoxIcon] = useState(["", " " , " " , " " , " " , " " , " " , " " , " "]); // 아이콘 표시  state
    const[pointer, setPointer] = useState(["", "" , "" , "" , "" , "" , "" , "" , ""]); // 이중클릭 방지 state
    const[player, setPlayer] = useState(true) // true일 경우 O턴 false일 경우X턴 클릭할시 !player
    const[activePlayer , setActivePlayer] = useState(["active-o", ""]) // O X 표시칸 
    const[gameEnd , setGameEnd ] = useState(false) // 게임종료 state
    const[winner, SetWinner] = useState(); // 승리플레이어 state
    const[hide, setHide] = useState(); // 숨김처리 state
    const[score, setScore] = useState([0, 0]);


    useEffect (() => {
        for(let i = 0; i < arrData.length; i++) {

            // 가로 판정
            const firstColumn = arrData[0] + arrData[1] + arrData[2]
            const secondColumn = arrData[3] + arrData[4] + arrData[5]
            const thirdColumn = arrData[6] + arrData[7] + arrData[8]

            // 세로 판정
            const firstRow = arrData[0] + arrData[3] + arrData[6]
            const secondRow = arrData[1] + arrData[4] + arrData[7]
            const thirdRow = arrData[2] + arrData[5] + arrData[8]

            // 대각선 판정
            const LeftCross =  arrData[0] + arrData[4] + arrData[8]
            const rightCross =  arrData[2] + arrData[4] + arrData[6]

            if((firstColumn == "OOO") || (secondColumn == "OOO") || (thirdColumn == "OOO") ||
               (firstColumn == "XXX") || (secondColumn == "XXX") || (thirdColumn == "XXX") ||
               (firstRow == "OOO") || (secondRow == "OOO") || (thirdRow == "OOO") ||
               (firstRow == "XXX") || (secondRow == "XXX") || (thirdRow == "XXX")  ||
               (LeftCross == "OOO" || (rightCross == "OOO")) ||
               (LeftCross == "XXX" || (rightCross == "XXX"))) {

                  // 승부가 났을경우
                  setGameEnd(true);

                if(player == false) {
                      // O 플레이어가 이겼을 경우
                      SetWinner(<VscCircle className="winner-o-icon"></VscCircle>)

                      let copyScore = [...score];
                      copyScore[0] = score[0] + 1;
                      setScore(copyScore);

                } else if(player == true) {
                      // X 플레이어가 이겼을 경우
                      SetWinner(<RxCross2 className="winner-x-icon"></RxCross2>)
                      setScore(score[1] + 1);

                      let copyScore = [...score];
                      copyScore[1] = score[1] + 1;
                      setScore(copyScore);
                      }
                
            } else {
                
                // 무승부 판정
                if(arrData.indexOf(0) == -1) {
                    setGameEnd(true);
                    setHide("hide")
                    SetWinner(<div className="winner-draw">무승부</div>)
                }
               
            }
        }
    },[arrData])

    return (
       <>
    {/* 게임종료 표시판*/}
        {
            gameEnd && <div className="gameEnd-wrapper">
          

            {winner}
          
            <p className={`win ${hide}`}>승리</p>
            <button className="restart-btn" onClick={() => {

                // 다시시작 (스코어를 제외한 모든 state 초기화)
                setGameEnd(false)

                let copySetArr = [...arrData];
                copySetArr = [0, 0, 0, 0, 0, 0, 0, 0, 0];
                setArrData(copySetArr)

                let copyOxIcon = [...oxIcon];
                copyOxIcon = ["", " " , " " , " " , " " , " " , " " , " " , " "];
                setoxIcon(copyOxIcon)

                let copyPointer = [...pointer];
                copyPointer = ["", "" , "" , "" , "" , "" , "" , "" , ""];
                setPointer(copyPointer)

                let copyActivePlayer = [...activePlayer];
                copyActivePlayer = ["active-o", ""];
                setActivePlayer(copyActivePlayer)

                setHide("");
                setPlayer(true);
                SetWinner("");


            }}>다시시작</button>
            
           
            </div>
        }

     {/* 플레이어 표시판*/}
        <div className={`show-player-box`}>
            <div className={`player-box o-player-box ${activePlayer[0]}`}>
               <VscCircle className="o-PlayerIcon"></VscCircle>
                <div>
                    <p className="score">{score[0]}</p>
                </div>
            </div>
            <div className={`player-box x-player-box ${activePlayer[1]}`}>
            <RxCross2 className="x-PlayerIcon"></RxCross2>
                <div>
                    <p className="score">{score[1]}</p>
                </div>
            </div>
        </div>

    {/* 뒷부분 배경 */}
        <div className="background-wrapper"/>


     {/* 게임판 */}
        <div className="main-wrapper">
       
        {
            arrData.map((a, i) => {
                return (
                    <div className={`item-card ${pointer[i]}`} key={i} onClick={() => {

                        //클릭시 플레이어 변경 
                        setPlayer(!player)

                        //같은칸 두번클릭시 클릭이벤트 방지
                        let copyPointer = [...pointer]
                        copyPointer[i] = 'pointer'
                        setPointer(copyPointer)

                        // O 플레이어일 경우
                        if(player == true) {

                            // 클릭할시 아이콘 세팅
                            let copyOxIcon = [...oxIcon]
                            copyOxIcon[i] = <VscCircle className="o-icon"></VscCircle>
                            setoxIcon(copyOxIcon)

                            // 플레이어 표시판 세팅
                            let copyActivePlayer = [...activePlayer]
                            copyActivePlayer = ["", "active-x"]
                            setActivePlayer(copyActivePlayer)

                            // 판정배열 채우기 X 플레이어일 경우 배열애 O추가
                            let copyArrData = [...arrData]
                            copyArrData[i] = "O"
                            setArrData(copyArrData)
                        // X 플레이어일 경우
                        } else {

                            // 클릭할시 아이콘 세팅
                            let copyOxIcon = [...oxIcon]
                            copyOxIcon[i] = <RxCross2 className="x-icon"></RxCross2>
                            setoxIcon(copyOxIcon)

                            // 플레이어 표시판 세팅
                            let copyActivePlayer = [...activePlayer]
                            copyActivePlayer = ["active-o", ""]
                            setActivePlayer(copyActivePlayer)

                            // 판정배열 채우기 X 플레이어일 경우 배열애 X추가
                            let copyArrData = [...arrData]
                            copyArrData[i] = "X"
                            setArrData(copyArrData)
                        }
                     }}>
                        <div className={`test2`}>
                        {oxIcon[i]}
                      </div>
                    </div>
                    )
                })
            }
        </div>
        </>
    )
}

export default Main